'use strict';

//partner.js
//a model scheme for our partner documents
const uuid = require('uuid');
const db = require('./db.js');
const table = "PartnerTable";

exports.create = function(params){
  return new Promise(function(resolve, reject) {
    
      var createParams = {
        TableName: table,
        Item:{
          "id": uuid.v1(),
          "firstName": params.firstName,
          "lastName": params.lastName,
          "percentage": params.percentage
        },
        ReturnValues: "ALL_OLD",
        ConsistentRead: true
      };

      db.create(createParams).then(function(item){
        resolve(item)
      });
    });
} 
 
exports.scan = function(){
    return new Promise(function(resolve, reject) {
      var scanParams = {
        TableName: table,
        ProjectionExpression: "firstName, lastName, percentage",
        ConsistentRead: 'true'
      };
      resolve(db.scan(scanParams, onScan));
    });
  }

  function onScan(err, data) {
    if (err) {
        console.error("Unable to scan the table. Error JSON:", JSON.stringify(err, null, 2));
    } else {        
        data.Items.forEach(function(itemdata) {
           console.log("Item :", ++count,JSON.stringify(itemdata));
        });

        if (typeof data.LastEvaluatedKey != "undefined") {
            params.ExclusiveStartKey = data.LastEvaluatedKey;
            db.scan(params, onScan);
        }
    }
}
